<template>
  <div :class="prefixCls">
    <p :class="`${prefixCls}__title`" v-if="$slots.title">
      <slot name="title" />
    </p>
    <div :class="`${prefixCls}__wrap`">
      <input
        :value="val"
        type="text"
        placeholder="0.0"
        class="placeholder-gray-440 text-gray-440"
        @input="handleInput($event)"
      />
      <div :class="`${prefixCls}__options`">
        <a
          href="javascript:;"
          v-if="all"
          class="cursor-pointer select-none text-sm"
          @click="handleChoiseAll"
        >
          全部</a
        >
        <span class="text-gray-440 ml-4 text-sm">USDT</span>
      </div>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { formateInput } from '@/utils';

  export default defineComponent({
    name: 'input',
    props: {
      all: {
        type: Boolean,
        default: true,
      },
      allValue: {
        type: String,
        default: '',
      },
      empty: {
        type: Number,
        default: 0,
      },
    },
    emits: ['update:value'],
    setup(props, { emit }) {
      const { prefixCls } = useDesign('input');
      const val = ref('');

      function handleChoiseAll() {
        val.value = props.allValue;
      }
      function handleInput(e) {
        const value = formateInput(e.target.value);
        e.target.value = value;
        val.value = value;
      }
      watch(
        () => props.empty,
        (newVal) => {
          console.log(newVal);

          val.value = '';
        }
      );
      watch(
        () => val.value,
        (val) => {
          emit('update:value', val);
        }
      );
      return { prefixCls, handleChoiseAll, val, handleInput };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-input';
  .@{prefix-cls} {
    &__title {
      @apply text-base text-gray-460 text-left;
    }
    &__wrap {
      @apply relative mt-1;
      height: 40px;
    }
    input {
      @apply rounded-base w-full border h-full outline-none pl-4 leading-none;
    }
    &__options {
      @apply absolute flex flex-row items-end leading-5 right-4 top-1/2 text-base;
      margin-top: -10px;
    }
  }
</style>
