
  import { defineComponent, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    name: 'deposit',
    props: {
      open: {
        type: Boolean,
      },
    },
    setup(_, { emit }) {
      const { prefixCls } = useDesign('deposit');
      const step = ref(1);
      const handleClose = () => {
        emit('close');
      };
      const handleClickNext = () => {
        step.value = 2;
      };

      return { prefixCls, step, handleClose, handleClickNext };
    },
  });
