import { VuexModule, getModule, Module, Mutation } from 'vuex-module-decorators';
import store from '@/store';
const NAME = 'modal';
type ModalState = Record<string, boolean>;
@Module({ dynamic: true, namespaced: true, store, name: NAME })
class Modal extends VuexModule {
  private modalState: ModalState = {
    connectors: false,
  };
  get getConnectorsModalState(): boolean {
    return this.modalState.connectors;
  }
  @Mutation
  setConnectorModalState(value: boolean): void {
    this.modalState.connectors = value;
  }
}
export const modalStore = getModule<Modal>(Modal);
