<template>
  <!-- @todo 退出登陆 -->
  <div :class="`${prefixCls}`" @click.stop="handleClick"
    >{{ address }}
    <div v-if="logout" class="logout anim-scale-in" @click.stop="handleLogout">退出</div></div
  >
</template>
<script lang="ts">
  import { computed, defineComponent, onBeforeUnmount, onMounted, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import { shortenAddress } from '@/helper/utils';
  import EventListener from '@/helper/eventListener';
  export default defineComponent({
    name: 'logo',
    setup() {
      let _clickEvent: any;
      const { prefixCls } = useDesign('wallet-address');
      const logout = ref(false);
      const address = computed(() => {
        return shortenAddress(accountStore.getAccountState.address || '');
      });
      function handleClick() {
        logout.value = !logout.value;
      }
      function handleLogout() {
        accountStore.disconnect();
      }
      onMounted(() => {
        _clickEvent = EventListener.listen(document.body, 'click', function () {
          logout.value = false;
        });
      });
      return { prefixCls, address, handleClick, handleLogout, logout };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-wallet-address';
  .@{prefix-cls} {
    @apply relative text-white text-base mr-8 cursor-pointer;
    &:before {
      @apply absolute block bg-green-500 rounded-full;
      width: 6px;
      height: 6px;
      content: '';
      margin-top: -3px;
      top: 50%;
      left: -16px;
    }
    .logout {
      @apply absolute shadow-sm bg-white text-gray-440 text-sm w-28 text-center rounded-base leading-8 cursor-pointer left-0 z-30;
    }
  }
</style>
