<template>
  <AppProvider>
    <router-view />
    <!-- 消息通知 -->
    <Notifications />
    <!-- 弹窗 -->
    <div id="modals"></div>
  </AppProvider>
</template>
<script lang="ts">
  import { defineComponent, onMounted, watch } from 'vue';
  import { AppProvider } from './components/Application';
  import { initAppConfigStore } from '@/logics/initAppConfig';
  import { useLocale } from '@/locales/useLocale';
  import { useLocaleSetting } from '@/locales/useLocaleSetting';
  import { useI18n } from '@/hooks/useI18n';
  import { LocaleType } from '@/locales/types';
  import { notifyStore } from '@/store/modules/notifications';
  import { ws } from '@/utils/http/ws/socket';
  import { accountStore } from '@/store/modules/account';
  import EventListener from '@/helper/eventListener';
  import Storage from '@/utils/storage';
  import { useRoute } from 'vue-router';
  export default defineComponent({
    name: 'App',
    components: {
      AppProvider,
    },
    setup() {
      const route = useRoute();
      watch(
        () => route.name,
        (val) => {
          switch (val) {
            case 'message':
              document.body.style.background = '#f5f5f5';
              break;
            case 'trade':
              document.body.style.background = '#222A3D';
              break;
            case 'home':
              document.body.style.background = '#080639';
              break;
            default:
              document.body.style.background = '';
              break;
          }
        }
      );
      // 初始化k线
      ws.initWebSocket();

      const { setLocale, changeLocale } = useLocale();
      // 支持语言列表
      const { localeList } = useLocaleSetting();
      // support Multi-language
      setLocale();
      // Initialize vuex internal system configuration
      initAppConfigStore();
      const { t } = useI18n();
      const handleChangeEvent = (event: LocaleType) => {
        changeLocale(event);
      };
      onMounted(() => {
        accountStore.init();
        EventListener.listen(window, 'beforeunload', function () {
          // 页面刷新未链接钱包提示
          Storage.set('connectorModal', 'pageReload');
          // Checking if Web3 has been injected by the browser (Mist/MetaMask)
          // const web3 = (window as any).web3;
          // if (typeof web3 === 'undefined') {
          //   notifyStore.commitNotify('请先在谷歌浏览器上安装metamask插件并导入自己的以太坊钱包');
          // }
        });
      });
      return { t, localeList, handleChangeEvent };
    },
  });
</script>
