import Storage from '@/utils/storage';

export const BASE_LOCAL_CACHE_KEY = 'LOCAL__CACHE__KEY__';

interface CacheStore {
  local: Recordable;
}

const cacheStore: CacheStore = {
  local: {},
};

function initCache() {
  cacheStore.local = Storage.get(BASE_LOCAL_CACHE_KEY) || {};
}

initCache();

export function setLocal(key: string, value: any): void {
  const local = Storage.get(BASE_LOCAL_CACHE_KEY)?.[BASE_LOCAL_CACHE_KEY] || {};
  cacheStore.local[BASE_LOCAL_CACHE_KEY] =
    { ...local, ...cacheStore.local[BASE_LOCAL_CACHE_KEY] } || {};
  cacheStore.local[BASE_LOCAL_CACHE_KEY][key] = value;
  Storage.set(BASE_LOCAL_CACHE_KEY, cacheStore.local);
}

export function getLocal<T>(key: string): T | null {
  try {
    return cacheStore.local[BASE_LOCAL_CACHE_KEY][key];
  } catch (error) {
    return null;
  }
}

export function removeLocal(key: string): void {
  if (cacheStore.local[BASE_LOCAL_CACHE_KEY]) {
    Reflect.deleteProperty(cacheStore.local[BASE_LOCAL_CACHE_KEY], key);
  }
}

export function clearLocal() {
  cacheStore.local = {};
  Storage.remove(BASE_LOCAL_CACHE_KEY);
}

export function persistentCache() {
  const localCache = cacheStore.local;
  Storage.set(BASE_LOCAL_CACHE_KEY, localCache);
}

() => {
  window.addEventListener('beforeunload', () => {
    persistentCache();
  });
};
