import { createRouter, createWebHistory, RouteRecordRaw, createWebHashHistory } from 'vue-router';
import { createProgress } from './progress';
const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: () => import(/* webpackChunkName: "home" */ '@/views/Home/Container.vue'),
  },
  {
    path: '/trade',
    name: 'trade',
    component: () => import(/* webpackChunkName: "trade" */ '@/views/Trade/Container.vue'),
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '@/views/About.vue'),
  },
  {
    path: '/fighting',
    name: 'fighting',
    component: () => import(/* webpackChunkName: "fighting" */ '@/views/Fighting/Container.vue'),
  },
  {
    path: '/message',
    name: 'message',
    component: () => import(/* webpackChunkName: "message" */ '@/views/MessageListAll.vue'),
  },
];

const router = createRouter({
  history: createWebHashHistory(), //createWebHistory(process.env.BASE_URL),
  routes,
});
createProgress(router);
export default router;
