import { defHttp } from '@/utils/http/axios';
enum Api {
  LIST = '/msg/list',
  READ = '/msg/read',
}

export function getMsgListApi(params) {
  return defHttp.request({
    url: Api.LIST,
    method: 'POST',
    params,
  });
}

export function setMsgReadApi(params) {
  return defHttp.request({
    url: Api.READ,
    method: 'POST',
    params,
  });
}
