import moment from 'moment';
export function shortenAddress(str = '') {
  return str ? `${str.slice(0, 6)}...${str.slice(str.length - 4)}` : str;
}

export function dataFormate(timestamp, short = '') {
  if (isNaN(parseInt(timestamp)) || !timestamp) return '--';
  if (short) {
    return short === 'ymd'
      ? moment(timestamp * 1000).format('YYYY-MM-DD')
      : moment(timestamp * 1000).format('HH:mm:ss');
  } else {
    return moment(timestamp * 1000).format('YYYY-MM-DD HH:mm:ss');
  }
}
