
  import { defineComponent } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    emits: ['click'],
    props: {
      loading: Boolean,
      disabled: Boolean,
    },
    setup(_, { emit }) {
      const { prefixCls } = useDesign('button');
      const handleClick = () => {
        emit('click');
      };
      return { prefixCls, handleClick };
    },
  });
