<template>
  <div class="page-navigation">
    <a
      href="javascript:;"
      :class="[current <= 1 ? 'disabled' : 'prevPage']"
      @click="setCurrent(current - 1)"
    >
      &lt;
    </a>
    <a
      href="javascript:;"
      :class="[current == page ? 'current' : '']"
      v-for="(page, index) in pageList"
      :key="index"
      @click="setCurrent(page)"
      >{{ page }}</a
    >
    <a
      href="javascript:;"
      :class="[current < totalPage ? 'nextPage' : 'disabled']"
      @click="setCurrent(current + 1)"
    >
      &gt;
    </a>
  </div>
</template>

<script lang="ts">
  import { computed, defineComponent, ref, watch, PropType } from 'vue';
  export default defineComponent({
    name: 'pageNavgiation',
    emits: ['update:currentPage'],
    props: {
      totalCount: {
        type: Number as PropType<number>,
        default: 0,
      },
      displayNum: {
        type: Number as PropType<number>,
        default: 0,
      },
      currentPage: {
        type: Number as PropType<number>,
        default: 0,
      },
      total: {
        type: Number as PropType<number>,
        default: 0,
      },
    },
    // Argument of type 'number' is not assignable to parameter of type 'object'.
    setup(props, { emit }) {
      const current = ref<number>(props.currentPage + 1);
      const totalPage = computed(() => {
        return props.total || Math.ceil(props.totalCount / props.displayNum);
      });
      const pageList = computed(() => {
        let currentVal = current.value,
          pageCount = totalPage.value,
          temp: any = [];
        //中间页码
        if (currentVal != 1 && currentVal >= 4 && pageCount != 4) temp.push(1);

        if (currentVal - 2 > 2 && currentVal <= pageCount && pageCount > 5) temp.push('...');

        let start = currentVal - 2,
          end = currentVal + 2;

        if ((start > 1 && currentVal < 4) || currentVal == 1) end++;

        if (currentVal > pageCount - 4 && currentVal >= pageCount) start--;

        for (; start <= end; start++) {
          if (start <= pageCount && start >= 1) temp.push(start);
        }

        if (currentVal + 2 < pageCount - 1 && currentVal >= 1 && pageCount > 5) temp.push('...');

        if (currentVal != pageCount && currentVal < pageCount - 2 && pageCount != 4)
          temp.push(pageCount);

        return temp;
      });
      watch([props.currentPage], ([val]) => {
        if (val < 0) {
          current.value = 1;
        } else {
          current.value = val + 1;
        }
      });
      function setCurrent(idx) {
        console.log(idx, ':', current.value);

        if (current.value != idx && idx > 0 && idx < totalPage.value + 1) {
          current.value = idx;
          emit('update:currentPage', current.value - 1);
        }
      }
      return { current, pageList, setCurrent, totalPage };
    },
  });
</script>

<style scoped lang="less">
  .page-navigation a,
  .page-navigation a.current,
  .page-navigation a.disabled {
    display: inline-block;
    margin-left: 10px;
    width: 30px;
    height: 30px;
    line-height: 30px;
    vertical-align: middle;
  }

  .page-navigation a {
    color: #666;
    font-size: 15px;
    text-align: center;
    border: 1px solid #dddee1;
    background: #fff;
    border-radius: 3px;
  }

  .page-navigation a:hover {
    text-decoration: none;
  }

  .page-navigation a.current {
    color: #fff;
    background: var(--link-color);
    border-color: var(--link-color);
    border-radius: 3px;
  }

  .page-navigation a.disabled {
    color: #bfbfbf;
    background: #f2f2f2;
    border: 1px solid #dddee1;
    border-radius: 3px;
  }
</style>
