<template>
  <div :class="`${prefixCls}`">
    <!-- <span  @click="handleLogin">链接钱包</span> -->
    <UiButton
      :loading="loading"
      :class="`${prefixCls}__connect`"
      class="hollow"
      @click="handleConnectors"
      >链接钱包</UiButton
    >
  </div>
</template>
<script lang="ts">
  import { defineComponent, computed, onMounted, onBeforeUnmount } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import { modalStore } from '@/store/modules/modal';
  import Storage from '@/utils/storage';
  export default defineComponent({
    name: 'accountStatus',
    setup() {
      const { prefixCls } = useDesign('wallet');
      let timer: any;
      const handleLogin = () => {
        accountStore.connect('injected');
      };
      const loading = computed(() => {
        // return accountStore.getAuthLoading;
        return false;
      });
      function handleConnectors() {
        modalStore.setConnectorModalState(true);
      }
      onMounted(() => {
        const flag = Storage.get('connectorModal');
        if (flag === 'pageReload') {
          timer = setTimeout(() => {
            modalStore.setConnectorModalState(true);
            Storage.remove('connectorModal');
          }, 2500);
        }
      });
      onBeforeUnmount(() => {
        clearTimeout(timer);
        Storage.remove('connectorModal');
      });
      return { prefixCls, handleConnectors, loading };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-wallet';
  .@{prefix-cls} {
    margin-right: 36px;
    &__connect {
      @apply block border text-sm rounded-base text-pink-410 border-pink-410 cursor-pointer select-none;
      height: 30px !important;
      line-height: 30px !important;
      padding: 0 14px;
    }
  }
</style>
