<template>
  <UiModal :open="open" @close="handleClose" style="max-width: 360px;">
    <div :class="prefixCls">
      <p class="mt-4 text-gray-470 text-left text-xl font-semibold">平仓</p>
      <ul :class="`${prefixCls}__info`">
        <li>
          <span>预计平仓价格</span>
          <strong>{{ closePrice }} USDT</strong>
        </li>
        <li>
          <span>保证金</span>
          <strong>{{ margin }} USDT</strong>
        </li>
        <li>
          <span>未实现盈亏</span>
          <strong>{{ unrealized }} USDT</strong>
        </li>
        <li>
          <span>手续费</span>
          <strong>101.23 USDT</strong>
        </li>
        <li>
          <span>预计收到抵押金</span>
          <strong>101.23 USDT</strong>
        </li>
      </ul>

      <div :class="`${prefixCls}__picker`">
        <p class="text-xs text-gray-460 text-left">滑点偏移公差</p>
        <div class="w-full flex flex-row justify-between text-xs text-gray-460 text-center mt-2">
          <span
            v-for="(item, index) in slippageList"
            :key="index"
            :class="[`${prefixCls}__picker--item`, slippage == item ? 'selected' : '']"
            @click="handleClick(item)"
            >{{ item }}%</span
          >
          <input
            :class="`${prefixCls}__picker--item`"
            type="text"
            placeholder="自定义"
            class="placeholder-gray-460 outline-none text-center"
            v-model="slippage"
          />
        </div>
      </div>

      <UiButton :loading="loading" class="mt-6 gradient" @click="handleClosePosition"
        >确定调整</UiButton
      >
    </div>
  </UiModal>
</template>
<script lang="ts">
  import { computed, defineComponent, ref, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import { Web3Provider } from '@ethersproject/providers';
  import ClearingHouse from '@/helper/clearingHouse';
  import { BigNumber as BN } from '@ethersproject/bignumber';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import { notifyStore, NotifyType } from '@/store/modules/notifications';
  import { parseEther, parseUnits } from '@ethersproject/units';
  import { bigNumberForamte } from '@/utils';
  export default defineComponent({
    name: 'closePosition',
    emits: ['close'],
    data() {
      return {
        slippageList: [0.01, 0.5, 1],
      };
    },
    props: {
      open: {
        type: Boolean,
      },
      address: {
        type: String,
        required: true,
      },
      positionInfo: {
        type: Object,
        default: null,
      },
    },
    setup(props: any, { emit }) {
      const { prefixCls } = useDesign('close-position');
      const slippage = ref(0.5);
      const handleClose = () => {
        emit('close');
      };
      const loading = computed(() => {
        return loadingBarStore.getLoadingState === LoadingStatus.CLOSE_POSITION;
      });
      const closePrice = computed(() => {
        return bigNumberForamte(props.positionInfo.closePrice, 2);
      });
      const margin = computed(() => {
        return bigNumberForamte(props.positionInfo.margin, 2);
      });
      const unrealized = computed(() => {
        return bigNumberForamte(props.positionInfo.unrealized, 2);
      });
      function handleClick(val: number) {
        slippage.value = val;
      }
      async function handleClosePosition() {
        try {
          loadingBarStore.setLoading(LoadingStatus.CLOSE_POSITION);
          const provider = (await accountStore.getProvider()) as Web3Provider;
          const tx = await ClearingHouse.closePosition(provider, props.address, BN.from(10));
          // {
          //   nonce: parseEther(slippage.value.toString()),
          // }
          await provider.waitForTransaction(tx.hash, 1);
          handleClose();
        } catch (error) {
          if (error.data && error.data.message) {
            notifyStore.commitNotify([error.data.message, NotifyType.ERROR]);
          } else {
            console.error(error);
          }
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      }
      return {
        prefixCls,
        slippage,
        handleClose,
        handleClick,
        handleClosePosition,
        loading,
        closePrice,
        margin,
        unrealized,
      };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-close-position';
  .@{prefix-cls} {
    @apply px-9 pb-8;
    &__info {
      @apply mt-4;
      li {
        @apply flex flex-row justify-between text-xs leading-8;
        span {
          @apply text-gray-460;
        }
        strong {
          @apply text-gray-470 font-normal;
        }
      }
    }
    input {
      &:focus {
        .border-gradient(#fff);
      }
    }
    &__picker {
      @apply mt-4;
      &--item {
        @apply border rounded-base cursor-pointer;
        width: 22%;
        line-height: 26px;
        &.selected {
          @apply text-gray-470 rounded-base;
          .border-gradient(#fff);
        }
      }
    }
  }
</style>
