
  import { defineComponent } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    name: 'fightingTips',
    emits: ['close'],
    props: {
      open: {
        type: Boolean,
      },
    },
    setup(_, { emit }) {
      const { prefixCls } = useDesign('fighting');

      function handleClose() {
        emit('close');
      }
      return { prefixCls, handleClose };
    },
  });
