<template>
  <UiModal :open="open" @close="handleClose" style="max-width: 360px;">
    <div :class="`${prefixCls}`">
      <div :class="`${prefixCls}__nav`">
        <div :class="`${prefixCls}__nav--item`" @click="handleChangeTab(Type.PLUS)"
          ><span :class="{ active: tabIndex === Type.PLUS }">增加保证金</span></div
        >
        <div :class="`${prefixCls}__nav--item`" @click="handleChangeTab(Type.MINUS)"
          ><span :class="{ active: tabIndex === Type.MINUS }">减少保证金</span></div
        >
      </div>
      <div class="mt-5">
        <UiInput
          :allValue="userBalance"
          @update:value="updateValue"
          :all="tabIndex === Type.PLUS"
          :empty="emptyFlag"
        >
          <template v-slot:title><span class="text-sm">保证金</span></template>
        </UiInput>
        <div class="text-xs leading-7 text-gray-460">
          <!-- 增加保证金 -->
          <template v-if="tabIndex === 0">
            <p class="flex flex-row justify-between">
              <span>钱包余额</span>
              <span>{{ userBalance }} USDT</span>
            </p>
          </template>

          <!-- 减少保证金 -->
          <template v-if="tabIndex === 1">
            <p class="flex flex-row justify-between"
              ><span>当前保证金</span>
              <span>{{ bigNumberForamte(actuallyMargin, 2) }} USDT</span>
            </p>
            <p class="flex flex-row justify-between"
              ><span>保证金率</span>
              <span>{{ bigNumberForamte(actuallyMarginRate) }}%</span>
            </p>
          </template>
        </div>
        <UiButton
          :loading="loading"
          :disabled="disabled"
          :class="disabled ? 'gray' : 'gradient'"
          class="mt-5"
          @click="adjustPositon()"
        >
          确认调整
        </UiButton>
        <p class="text-red-410 text-xs leading-4 mt-2">{{ errorText }}</p>
      </div>
    </div>
  </UiModal>
</template>
<script lang="ts">
  import { computed, defineComponent, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import BigNumber from 'bignumber.js';
  import { scale } from '@/utils';
  import ClearingHouse from '@/helper/clearingHouse';
  import { Web3Provider } from '@ethersproject/providers';
  import { BigNumber as BN } from '@ethersproject/bignumber';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import { notifyStore, NotifyType } from '@/store/modules/notifications';
  import { validateNumberInput, ValidationError, PositionValidation } from '@/utils/validation';
  import { bigNumberForamte } from '@/utils';
  enum Type {
    PLUS,
    MINUS,
  }
  export default defineComponent({
    name: 'earnestMoney',
    props: {
      open: {
        type: Boolean,
      },
      address: {
        type: String,
        default: '',
      },
      positionInfo: {
        type: Object,
        default: null,
      },
    },
    setup(props, { emit }) {
      const tabIndex = ref(Type.PLUS);
      const { prefixCls } = useDesign('earnest');
      const inputVal = ref('0');
      const emptyFlag = ref(0);
      const userBalance = computed(() => {
        const { balances, address } = accountStore.getAccountState;
        if (!address) return 0;
        return scale(new BigNumber(balances[address].toString()), -6).toNumber();
      });
      const validation = computed(() => {
        const inputError = validateNumberInput(inputVal.value);
        if (inputError === ValidationError.EMPTY) {
          return PositionValidation.EMPTY_INPUT;
        }
        if (inputError !== ValidationError.NONE) {
          return PositionValidation.INVALID_INPUT;
        }
        if (new BigNumber(inputVal.value).gt(new BigNumber(userBalance.value?.toString()))) {
          return PositionValidation.INSUFFICIENT_BALANCE;
        }
        return PositionValidation.NONE;
      });
      const disabled = computed(() => {
        if (validation.value !== PositionValidation.NONE) return true;
        return false;
      });
      const errorText = computed(() => {
        if (validation.value === PositionValidation.INSUFFICIENT_BALANCE) {
          return '资金不足';
        }
        return '';
      });
      const actuallyMargin = computed(() => {
        const inputValBn = new BigNumber(inputVal.value).isNaN()
          ? new BigNumber(0)
          : new BigNumber(inputVal.value);
        return new BigNumber(props.positionInfo.margin).minus(scale(inputValBn, 18)).toString();
      });
      const actuallyMarginRate = computed(() => {
        const inputValBn = new BigNumber(inputVal.value);
        if (inputValBn.isNaN() || inputValBn.isEqualTo(0)) return props.positionInfo.marginRate;
        return new BigNumber(actuallyMargin.value)
          .plus(new BigNumber(props.positionInfo.unrealized))
          .div(
            new BigNumber(props.positionInfo.openPrice).times(
              new BigNumber(props.positionInfo.volume)
            )
          )
          .toString();
      });
      const handleClose = () => {
        emit('close');
      };
      const handleChangeTab = (index: Type) => {
        tabIndex.value = index;
        inputVal.value = '';
        emptyFlag.value = emptyFlag.value + 1;
      };
      const loading = computed(() => {
        return loadingBarStore.getLoadingState === LoadingStatus.ADJUST_MARGIN;
      });
      async function adjustPositon() {
        const provider = (await accountStore.getProvider()) as Web3Provider;
        let tx: any;
        try {
          loadingBarStore.setLoading(LoadingStatus.ADJUST_MARGIN);
          if (tabIndex.value === Type.PLUS) {
            tx = await ClearingHouse.addMargin(
              provider,
              props.address,
              BN.from(
                new BigNumber(inputVal.value).multipliedBy(new BigNumber(10).pow(6)).toString()
              )
            );
          }

          if (tabIndex.value === Type.MINUS) {
            tx = await ClearingHouse.removeMargin(
              provider,
              props.address,
              BN.from(
                new BigNumber(inputVal.value).multipliedBy(new BigNumber(10).pow(6)).toString()
              )
            );
          }
          // @note 等待交易完成
          await provider.waitForTransaction(tx.hash, 1);
          handleClose();
        } catch (error) {
          if (error.data && error.data.message) {
            notifyStore.commitNotify([error.data.message, NotifyType.ERROR]);
          } else {
            console.error(error);
          }
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      }
      function updateValue(data) {
        inputVal.value = data;
      }
      return {
        prefixCls,
        handleClose,
        handleChangeTab,
        tabIndex,
        Type,
        userBalance,
        adjustPositon,
        loading,
        updateValue,
        errorText,
        disabled,
        actuallyMargin,
        actuallyMarginRate,
        bigNumberForamte,
        emptyFlag,
      };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-earnest';
  .@{prefix-cls} {
    @apply px-9 pb-8 pt-8;
    &__nav {
      @apply flex flex-row;
      &--item {
        @apply flex-1 text-center;
        span {
          @apply relative leading-7 pb-2 cursor-pointer text-center text-gray-440;
          &::after {
            @apply block absolute bottom-0;
            width: 80%;
            height: 3px;
            content: '';
            left: 50%;
            transform: translateX(-50%);
          }
          &.active {
            @apply text-gray-470;
            &:after {
              .bg-gradient();
            }
          }
        }
      }
    }
  }
</style>
