<template>
  <div :class="`${prefixCls}__lists`" class="anim-scale-in" @click.stop="">
    <div :class="`${prefixCls}__lists--header`">
      <h3>站内信</h3>
      <router-link to="/message">查看全部</router-link>
    </div>
    <div :class="`${prefixCls}__lists--con`">
      <ul>
        <template v-for="({ content, status, time, title }, index) in filterList" :key="index">
          <li :class="{ reading: status }">
            <div class="top">
              <span class="type">{{ title }}</span>
              <span class="time">{{ dataFormate(time, 'ymd') }}</span>
            </div>
            <p>{{ content }}</p>
          </li>
        </template>
      </ul>
    </div>
    <div :class="`${prefixCls}__lists--footer`">
      <span>
        <input type="checkbox" id="hide" v-model="hideRead" /><label for="hide"
          >隐藏已读</label
        ></span
      >
      <a href="javascript:;" @click="handleRead(0)">全部标为已读</a>
    </div>
  </div>
</template>
<script lang="ts">
  import { computed, defineComponent, onMounted, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { getMsgListApi, setMsgReadApi } from '@/api/trade/msssage';
  import { dataFormate } from '@/helper/utils';
  export default defineComponent({
    name: 'messageLists',
    setup() {
      const { prefixCls } = useDesign('message');
      const lists = ref([]);
      const hideRead = ref(false);
      const filterList = computed(() => {
        if (!hideRead.value) return lists.value;
        return lists.value.filter((item: any) => !item.status);
      });
      async function handleRead(msgId) {
        try {
          const result = await setMsgReadApi({ mid: msgId });
          lists.value.map((item: any) => (item.status = 1));
        } catch (error) {
          console.error(error);
        }
      }
      onMounted(async () => {
        const result = await getMsgListApi({ page: 1, limit: 5 });
        lists.value = result.list;
      });
      return { prefixCls, filterList, dataFormate, hideRead, handleRead };
    },
  });
</script>
