        @apply flex flex-row justify-between items-end;
        h3 {
          @apply mb-0 leading-none text-base text-gray-470;
        }
        a {
          @apply text-xs leading-none;
        }
      }
      &--con {
        height: 375px;
        overflow-y: auto;
        margin-top: 20px;
        padding-bottom: 10px;
        padding-right: 28px;
        padding-left: 28px;
        .reading {
          .top {
            .type {
              &:before {
                display: none;
              }
              @apply text-gray-470;
            }
          }
          p {
            @apply text-gray-450;
          }
        }

        .top {
          @apply flex flex-row justify-between;
          .type {
            @apply text-sm text-gray-470 leading-none relative;
            &:before {
              width: 6px;
              height: 6px;
              content: '';
              top: 4px;
              left: -12px;
              @apply block absolute rounded-full bg-red-420;
            }
          }
          .time {
            @apply text-gray-460 text-xs leading-none;
          }
        }
        p {
          @apply text-xs leading-4 text-gray-440 mt-2;
          .ellipsis-multiple(2);
        }
      }
      &--footer {
        padding-right: 28px;
        padding-left: 28px;
        @apply flex flex-row justify-between items-center mt-5;
        span {
          @apply flex flex-row items-center text-xs;
          input {
            @apply mr-1;
          }
        }
