import type { App } from 'vue';
import { upperFirst, camelCase } from 'lodash-es';
export function setupComponent(app: App): void {
  const requireComponent = require.context('.', true, /\.vue$/);
  requireComponent.keys().forEach((file) => {
    const componentCfg = requireComponent(file);
    const componentName = upperFirst(camelCase(file.replace(/^\.\//, '').replace(/\.\w+$/, '')));
    app.component(componentName, componentCfg.default || componentCfg);
  });
}

const svg = require.context('@/assets/svg', false, /\.svg$/);
const requireAllSvg = (requireContext) => requireContext.keys().map(requireContext);
requireAllSvg(svg);
