import { Lock } from '@snapshot-labs/lock/src';
import injected from '@snapshot-labs/lock/connectors/injected';
import fortmatic from '@snapshot-labs/lock/connectors/fortmatic';
import portis from '@snapshot-labs/lock/connectors/portis';
import walletconnect from '@snapshot-labs/lock/connectors/walletconnect';
import walletlink from '@snapshot-labs/lock/connectors/walletlink';

const defaultLogo = require('@/assets/connector/default.svg');
const fortmaticLogo = require('@/assets/connector/fortmatic.svg');
const frameLogo = require('@/assets/connector/frame.svg');
const imtokenLogo = require('@/assets/connector/imtoken.svg');
const metamaskLogo = require('@/assets/connector/metamask.svg');
const portisLogo = require('@/assets/connector/portis.svg');
const statusLogo = require('@/assets/connector/status.svg');
const trustwalletLogo = require('@/assets/connector/trustwallet.svg');
const walletconnectLogo = require('@/assets/connector/walletconnect.svg');
const walletlinkLogo = require('@/assets/connector/walletlink.svg');

import config from '@/config';

const lock = new Lock();

const connectors = { injected, fortmatic, portis, walletconnect, walletlink };

for (const connectorId in connectors) {
  const connector = {
    key: connectorId,
    connector: connectors[connectorId],
    options: config.connectors[connectorId],
  };
  lock.addConnector(connector);
}

export function hasInjectedProvider(): boolean {
  return !!window.ethereum;
}

export function getConnectorName(connectorId: string): string {
  if (connectorId === 'injected') {
    const provider = window.ethereum as any;
    if (provider.isMetaMask) {
      return 'MetaMask';
    }
    if (provider.isImToken) {
      return 'imToken';
    }
    if (provider.isStatus) {
      return 'Status';
    }
    if (provider.isTrust) {
      return 'Trust Wallet';
    }
    if (provider.isFrame) {
      return 'Frame';
    }
    return 'Browser Wallet';
  }
  if (connectorId === 'fortmatic') {
    return 'Fortmatic';
  }
  if (connectorId === 'portis') {
    return 'Portis';
  }
  if (connectorId === 'walletconnect') {
    return 'WalletConnect';
  }
  if (connectorId === 'walletlink') {
    return 'Coinbase Wallet';
  }
  return 'Unknown';
}

export function getConnectorLogo(connectorId: string): string {
  if (connectorId === 'injected') {
    const provider = window.ethereum as any;
    if (provider.isMetaMask) {
      return metamaskLogo;
    }
    if (provider.isImToken) {
      return imtokenLogo;
    }
    if (provider.isStatus) {
      return statusLogo;
    }
    if (provider.isTrust) {
      return trustwalletLogo;
    }
    if (provider.isFrame) {
      return frameLogo;
    }
    return defaultLogo;
  }
  if (connectorId === 'fortmatic') {
    return fortmaticLogo;
  }
  if (connectorId === 'portis') {
    return portisLogo;
  }
  if (connectorId === 'walletconnect') {
    return walletconnectLogo;
  }
  if (connectorId === 'walletlink') {
    return walletlinkLogo;
  }
  return '';
}

export default lock;
