import provider from '@/helper/provider';
import { Contract } from '@ethersproject/contracts';
import erc20Abi from '@/helper/abi/MockERC20.json';
import config from '@/config';
export type Allowances = Record<string, Record<string, string>>;
export type Balances = Record<string, string>;
export default class Assets {
  // constructor
  static async fetchAssets(address: string) {
    const contract = new Contract(config.address.assets, erc20Abi, provider);
    const proxyAddress = config.address.clearingHouse;
    const balance = await contract.balanceOf(address);
    const name = await contract.name();
    const symbol = await contract.symbol();
    const decimals = await contract.decimals();
    const allowance = await contract.allowance(address, proxyAddress);
    console.log(allowance);

    return {
      name,
      symbol,
      decimals,
      balance,
      allowance: { [proxyAddress]: { [config.address.assets]: allowance } },
    };
  }
}
