<template>
  <UiModal :open="open" @close="handleClose" style="max-width: 360px;">
    <div :class="prefixCls">
      <p :class="`${prefixCls}__title`">欢迎加入我们<br />请先连接钱包以进行交易</p>
      <div class="mt-5 cursor-pointer img-wrap" @click="handleLogin">
        <img
          :class="`${prefixCls}__logo`"
          :src="require('@/assets/connector/metamask.svg')"
          alt=""
        />
      </div>
      <p :class="`${prefixCls}__name`">MateMask</p>
    </div>
  </UiModal>
</template>

<script lang="ts">
  import { defineComponent } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import { notifyStore, NotifyType } from '@/store/modules/notifications';
  export default defineComponent({
    name: 'connectors',
    props: {
      open: {
        type: Boolean,
      },
    },
    setup(_, { emit }) {
      const { prefixCls } = useDesign('connectors');
      function handleClose() {
        emit('close');
      }
      async function handleLogin() {
        handleClose();
        if (window['ethereum']) {
          // provider = window['ethereum'];
          try {
            const res = await window['ethereum'].enable();
            console.log(res);
          } catch (e) {
            notifyStore.commitNotify([e.message, NotifyType.ERROR]);
            return;
            // if (e.code === 4001) return;
          }
        } else if (window['web3']) {
          // provider = window['web3'].currentProvider;
        } else {
          notifyStore.commitNotify([
            '请先在谷歌浏览器上安装metamask插件并导入自己的以太坊钱包',
            NotifyType.ERROR,
          ]);
          return;
        }
        try {
          loadingBarStore.setLoading(LoadingStatus.CONNECTOR);
          const result = await accountStore.connect('injected');
        } catch (error) {
          console.error(error.msg);
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      }
      return { prefixCls, handleClose, handleLogin };
    },
  });
</script>

<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-connectors';
  .@{prefix-cls} {
    @apply pb-9;
    &__title {
      @apply text-lg text-center leading-6 mt-7;
    }
    .img-wrap {
      @apply flex justify-center items-center rounded-full m-auto bg-gray-420 mt-5;
      width: 70px;
      height: 70px;
    }
    &__logo {
      width: 40px;
    }
    &__name {
      @apply text-base leading-8 text-center;
    }
  }
</style>
