import { deepMerge } from '@/utils';
import { updateGrayMode } from './theme';
import { getLocal } from '@/helper/persistent';
import projectSetting from '@/projectSetting';
import type { ProjectConfig } from '@/types/config';
import { PROJ_CFG_KEY, appStore } from '@/store/modules/app';

export function initAppConfigStore() {
  let projCfg: ProjectConfig = getLocal(PROJ_CFG_KEY) as ProjectConfig;
  projCfg = deepMerge(projectSetting, projCfg || {});
  try {
    const { grayMode } = projCfg;
    grayMode && updateGrayMode(grayMode);
  } catch (error) {
    console.log(error);

  }
  appStore.commitProjectConfigState(projCfg);
}