import homestead from './homestead';
import heco from './heco';
interface Connector {
  id: string;
  name: string;
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  options: any;
}

export interface Config {
  env: string;
  readonly chainId: number;
  readonly network: string;

  defaultPrecision: number;
  precision: number;

  explorer: string;
  subgraphurl: string;
  subgraphBackupUrl: string;
  rpcUrl: string;
  wsUrl: string;

  address: {
    [key: string]: string;
  };

  connectors: Record<string, Connector>;
}
enum Network {
  mainnet = 'homestead',
  heco = 'heco',
}

function getConfigByNetwork(network: Network | undefined): Config {
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  let config: any;

  switch (network) {
    case Network.heco:
      config = heco;
      break;
    case Network.mainnet:
      config = homestead;
      break;
    default:
      break;
  }
  return config as Config;
}

const config: Config = getConfigByNetwork((process.env.VUE_APP_NETWORK || 'homestead') as Network);

config.env = process.env.VUE_APP_NETWORK || 'heco';

export default config;
