import store from '@/store';
import { VuexModule, getModule, Module, Mutation, Action } from 'vuex-module-decorators';
import { Web3Provider, Provider } from '@ethersproject/providers';
import lock, { getConnectorName, getConnectorLogo } from '@/helper/connectors';
import provider from '@/helper/provider';
import Storage from '@/utils/storage';
import Assets, { Balances, Allowances } from '@/helper/assets';
enum TransactionStatus {
  PENDING,
  OK,
  FAILED,
}
interface Connector {
  id: string;
  name: string;
  logo: any;
}

export interface Transaction {
  text: string;
  hash: string;
  status: TransactionStatus;
  timestamp: number;
}
interface MetaData {
  address: string;
  name: string;
  symbol: string;
  decimals: number;
}

export interface AccountState {
  connector: Connector;
  address: string;
  chainId: number;
  proxy: string;
  balances: Balances;
  allowances: Allowances;
  transactions: Record<string, Transaction>;
  metaData: Record<string, MetaData>;
}

const NAME = 'account';
@Module({ dynamic: true, namespaced: true, store, name: NAME })
class AccountInfo extends VuexModule {
  public accountState = {
    connector: { id: '', name: '', logo: '' },
    address: '',
    chainId: 0,
    proxy: '',
    balances: {},
    allowances: {},
    transactions: {},
    metaData: {},
  };

  get getAccountState(): AccountState {
    return this.accountState;
  }
  get getToken(): string {
    return this.accountState.address || Storage.get('token');
  }
  @Mutation
  setConnector(connector: Connector): void {
    this.accountState.connector = connector;
  }
  @Mutation
  setAddress(address: string): void {
    this.accountState.address = address;
    Storage.set('token', address);
  }
  @Mutation
  setChainId(chainId: number): void {
    this.accountState.chainId = chainId;
  }
  setProxy(proxy: string): void {
    this.accountState.proxy = proxy;
  }
  @Mutation
  setMetaData(metaData: Record<string, MetaData>): void {
    this.accountState.metaData = metaData;
  }
  @Mutation
  addBalances(balances: Balances): void {
    for (const address in balances) {
      this.accountState.balances[address] = balances[address];
    }
  }
  @Mutation
  addAllowances(allowances: Allowances): void {
    for (const spender in allowances) {
      if (!this.accountState.allowances[spender]) {
        this.accountState.allowances[spender] = {};
      }
      for (const asset in allowances[spender]) {
        const allowance = allowances[spender][asset];
        this.accountState.allowances[spender][asset] = allowance;
      }
    }
  }
  @Mutation
  setTransactions(transactions: Record<string, Transaction>): void {
    this.accountState.transactions = transactions;
  }
  @Mutation
  setTransaction(transaction: Transaction): void {
    this.accountState.transactions[transaction.hash] = transaction;
  }
  @Mutation
  clearTransactions(): void {
    this.accountState.transactions = {};
  }
  @Mutation
  clear(): void {
    this.accountState.proxy = '';
    this.accountState.balances = {};
    this.accountState.allowances = {};
    this.accountState.transactions = {};
    this.accountState.metaData = {};
    Storage.set('token', '');
  }

  @Action
  async init(): Promise<void> {
    // Save Web3 provider if available
    const connectorId = Storage.get('connector');
    this.connect(connectorId);
  }
  @Action
  async fetchAssets(address: string): Promise<void> {
    if (!address) {
      return;
    }
    console.time(`[API] fetchAssets: ${address}`);
    const { name, symbol, decimals, balance, allowance } = (await Assets.fetchAssets(
      address
    )) as any;
    console.timeEnd(`[API] fetchAssets: ${address}`);
    this.addBalances({ [address]: balance });
    this.addAllowances(allowance);
    this.setMetaData({ [address]: { address, name, symbol, decimals } });
  }
  @Action
  async connect(connectorId: string): Promise<void> {
    if (!connectorId) {
      return;
    }

    const connector = lock.getConnector(connectorId);

    if (!connector) {
      return;
    }
    this.setConnector({
      id: connectorId,
      name: getConnectorName(connectorId),
      logo: getConnectorLogo(connectorId),
    });
    const provider = (await connector.connect()) as any;

    if (!provider) {
      this.disconnect();
      return;
    }
    const web3Provider = new Web3Provider(provider);
    const accounts = await web3Provider.listAccounts();
    if (accounts.length === 0) {
      this.disconnect();
      return;
    }
    this.saveProvider(provider);
    Storage.set('connector', connectorId);
  }
  @Action
  async disconnect(): Promise<void> {
    const connectorId = Storage.get('connector');
    if (connectorId) {
      const connector = lock.getConnector(connectorId);
      const isLoggedIn = connector.isLoggedIn();
      if (isLoggedIn) {
        await connector.logout();
      }
      Storage.set('connector', '');
    }
    this.setConnector({ id: '', name: '', logo: '' });
    this.setAddress('');
    this.setChainId(0);
    this.clear();
  }
  @Action
  async getProvider(): Promise<Provider> {
    if (this.accountState.connector && this.accountState.connector.id) {
      const connector = lock.getConnector(this.accountState.connector.id);
      const provider = (await connector.connect()) as any;

      return new Web3Provider(provider);
    }

    return provider;
  }
  @Action
  async saveProvider(provider: any): Promise<void> {
    if (provider.removeAllListeners) {
      provider.removeAllListeners();
    }
    console.log(provider);

    if (provider && provider.on) {
      provider.on('chainChanged', async () => {
        this.clear();
        this.saveProvider(provider);
      });
      provider.on('accountsChanged', async () => {
        this.clear();
        this.saveProvider(provider);
      });
      provider.on('disconnect', async () => {
        this.disconnect();
      });
    }
    const web3Provider = new Web3Provider(provider);
    const network = await web3Provider.getNetwork();
    const accounts = await web3Provider.listAccounts();
    const account = accounts[0];
    // const transactions = Storage.getTransactions(account, network.chainId);
    this.setAddress(account);
    this.setChainId(network.chainId);
    // commit('setTransactions', transactions);
    // dispatch('fetchState');
  }
}

export const accountStore = getModule<AccountInfo>(AccountInfo);
