<template>
  <button
    class="ripple"
    @click.stop="handleClick"
    :class="`${prefixCls}`"
    :disabled="loading || disabled"
  >
    <UiLoading class="flex-auto" v-if="loading" />
    <slot v-else />
  </button>
</template>
<script lang="ts">
  import { defineComponent } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    emits: ['click'],
    props: {
      loading: Boolean,
      disabled: Boolean,
    },
    setup(_, { emit }) {
      const { prefixCls } = useDesign('button');
      const handleClick = () => {
        emit('click');
      };
      return { prefixCls, handleClick };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls: ~'@{namespace}-button';
  .@{prefix-cls} {
    @apply block w-full border border-transparent text-white rounded-base outline-none text-base transition-all;
    height: 40px;
    &.gradient {
      @apply border-none;
      .bg-gradient();
    }
    &.hollow {
      @apply text-pink-410 bg-transparent border-pink-410;
    }
    &.gray {
      @apply bg-gray-430 text-gray-460 border-gray-430 cursor-not-allowed;
    }
    &.green {
      @apply bg-green-410 border-green-410 hover:bg-green-420 hover:border-green-420;
    }
    &.red {
      @apply bg-red-410 border-red-410 hover:bg-red-420 hover:border-red-420;
    }
    &.disabled {
      @apply bg-gray-450 border-gray-450 cursor-not-allowed hover:border-gray-450 hover:bg-gray-450;
    }
    &.ripple {
      position: relative;
      //隐藏溢出的径向渐变背景
      overflow: hidden;
      &:after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        pointer-events: none;
        //设置径向渐变
        background-image: radial-gradient(circle, #666 10%, transparent 10.01%);
        background-repeat: no-repeat;
        background-position: 50%;
        transform: scale(10, 10);
        opacity: 0;
        transition: transform 0.3s, opacity 0.5s;
      }
      &:active {
        &:after {
          transform: scale(0, 0);
          opacity: 0.3;
          //设置初始状态
          transition: 0s;
        }
      }
    }
  }
</style>
