<template>
  <div :class="`${prefixCls}`">
    <!-- @todo 站内信列表展示 -->
    <div :class="`${prefixCls}__num`" @click.stop="handleClick">
      <svg-icon iconClass="bell" />
      <div class="wrap" v-show="show"><MessageList /></div>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, onBeforeUnmount, onMounted, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import EventListener from '@/helper/eventListener';
  export default defineComponent({
    name: 'websiteMessage',
    setup() {
      const { prefixCls } = useDesign('message');
      const show = ref(false);
      function handleClick() {
        show.value = !show.value;
      }
      onMounted(() => {
        EventListener.listen(document.body, 'click', function () {
          show.value = false;
        });
      });
      return { prefixCls, show, handleClick };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-message';
  .@{prefix-cls} {
    margin-right: 36px;
    &__num {
      @apply relative cursor-pointer select-none;
      .wrap {
        position: absolute;
        right: 0;
        top: 40px;
        @apply z-20;
      }
      &:after {
        width: 6px;
        height: 6px;
        right: -2px;
        top: -2px;
        content: '';
        @apply block bg-red-600 absolute rounded-full;
      }
    }
  }
</style>
