import { useI18n } from '@/hooks/useI18n';
// const { createMessage } = useMessage();

// const error = createMessage.alert!;
export function checkStatus(status: number, msg: string): void {
  const { t } = useI18n();
  switch (status) {
    case 400:
      alert(`${msg}`);
      break;
    case 403:
      alert(t('sys.api.errMsg403'));
      break;
    // 404请求不存在
    case 404:
      alert(t('sys.api.errMsg404'));
      break;
    case 405:
      alert(t('sys.api.errMsg405'));
      break;
    case 408:
      alert(t('sys.api.errMsg408'));
      break;
    case 500:
      alert(t('sys.api.errMsg500'));
      break;
    case 501:
      alert(t('sys.api.errMsg501'));
      break;
    case 502:
      alert(t('sys.api.errMsg502'));
      break;
    case 503:
      alert(t('sys.api.errMsg503'));
      break;
    case 504:
      alert(t('sys.api.errMsg504'));
      break;
    case 505:
      alert(t('sys.api.errMsg505'));
      break;
    default:
  }
}
