<template>
  <div style="position: relative;">
    <div ref="triger" class="select-none cursor-pointer hover:text-white">
      <slot> </slot>
    </div>
    <div
      class="anim-scale-in"
      :class="[
        prefixCls,
        show === true ? 'visible' : '',
        placement === 'top' ? 'top' : '',
        placement === 'left' ? 'left' : '',
        placement === 'right' ? 'right' : '',
        placement === 'bottom' ? 'bittom' : '',
      ]"
      ref="popover"
      role="tooltip"
    >
      <!-- <div :class="`${prefixCls}__arrow`"></div> -->
      <div :class="`${prefixCls}__inner`"> <slot name="content"></slot></div>
    </div>
  </div>
</template>
<script lang="ts">
  import {
    defineComponent,
    nextTick,
    onBeforeUnmount,
    onMounted,
    reactive,
    ref,
    toRefs,
    watch,
  } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import EventListener from '@/helper/eventListener';
  export default defineComponent({
    name: 'tooltips',
    props: {
      // 需要监听的事件
      trigger: {
        type: String,
        default: 'click',
      },
      // toolTip消息提示
      content: {
        type: String,
      },
      placement: {
        type: String,
      },
    },
    setup(props, { emit }) {
      const position = reactive({
        top: 0,
        left: 0,
      });

      const show = ref(false);
      const popover = ref();
      const triger = ref();
      const { prefixCls } = useDesign('tooltips');
      let _mouseenterEvent: any = null;
      let _mouseleaveEvent: any = null;

      let _focusEvent: any = null;
      let _blurEvent: any = null;
      let _clickEvent: any = null;
      function toggle() {
        show.value = !show.value;
      }
      watch(show, (val) => {
        if (val) {
          nextTick(() => {
            switch (props.placement) {
              case 'top':
                position.left = (triger.value.offsetWidth - popover.value.offsetWidth) / 2;
                position.top = triger.value.offsetTop - popover.value.offsetHeight - 6;
                break;
              case 'left':
                position.left = triger.value.offsetLeft - popover.value.offsetWidth;
                position.top =
                  triger.value.offsetTop +
                  triger.value.offsetHeight / 2 -
                  popover.value.offsetHeight / 2;
                break;
              case 'right':
                position.left = triger.value.offsetLeft + triger.value.offsetWidth;
                position.top =
                  triger.value.offsetTop +
                  triger.value.offsetHeight / 2 -
                  popover.value.offsetHeight / 2;
                break;
              case 'bottom':
                position.left =
                  triger.value.offsetLeft -
                  popover.value.offsetWidth / 2 +
                  triger.value.offsetWidth / 2;
                position.top = triger.value.offsetTop + triger.value.offsetHeight;
                break;
              default:
                console.log('Wrong placement prop');
            }
            popover.value.style.top = position.top + 'px';
            popover.value.style.left = position.left + 'px';
          });
        }
      });
      onMounted(() => {
        if (!popover.value)
          return console.error(
            "Couldn't find popover ref in your component that uses popoverMixin."
          );
        // 获取监听对象
        const _triger = triger.value.children[0];
        // 根据trigger监听特定事件
        if (props.trigger === 'hover') {
          _mouseenterEvent = EventListener.listen(_triger, 'mouseenter', () => {
            show.value = true;
          });
          _mouseleaveEvent = EventListener.listen(_triger, 'mouseleave', () => {
            const timer = setTimeout(() => {
              show.value = false;
            }, 180);
            EventListener.listen(popover.value, 'mouseenter', () => {
              clearTimeout(timer);
            });
            EventListener.listen(popover.value, 'mouseleave', () => {
              show.value = false;
            });
          });
        } else if (props.trigger === 'focus') {
          _focusEvent = EventListener.listen(_triger, 'focus', () => {
            show.value = true;
          });
          _blurEvent = EventListener.listen(_triger, 'blur', () => {
            show.value = false;
          });
        } else {
          _clickEvent = EventListener.listen(_triger, 'click', toggle);
        }
        // show.value = !show.value;
      });
      onBeforeUnmount(() => {
        if (_blurEvent) {
          _blurEvent.remove();
          _focusEvent.remove();
        }
        if (_mouseenterEvent) {
          _mouseenterEvent.remove();
          _mouseleaveEvent.remove();
        }
        if (_clickEvent) {
          _clickEvent.remove();
        }
      });
      return { prefixCls, triger, popover, show };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-tooltips';
  .@{prefix-cls} {
    position: absolute;
    display: none;
    &.visible {
      display: block;
    }
    &__arrow {
    }
    &__inner {
      @apply bg-white text-center text-xs shadow-sm text-gray-470 px-3 py-3 rounded-sm relative;
      &:before {
        display: block;
        content: '';
        width: 0;
        height: 0;
        position: absolute;
        border-style: solid;
        border-width: 6px;
      }
    }
    &.top {
      .@{prefix-cls} {
        &__inner {
          @apply whitespace-nowrap leading-4;
          &:before {
            border-color: #fff transparent transparent transparent;
            bottom: -12px;
            left: 50%;
            margin-left: -6px;
          }
        }
      }
    }
  }
</style>
