<template>
  <div :class="`${prefixCls}`" class="pb-1 relative">
    <LoadingBar v-if="loadingBar !== LoadingStatus.NONE" />
    <div :class="`${prefixCls}__main`">
      <div :class="`${prefixCls}__content--left`">
        <Logo />
        <Nav />
      </div>
      <div>
        <ConnectWallet v-if="!account" />
        <NavInfo v-else />
      </div>
    </div>
  </div>
</template>
<script lang="ts">
  import { defineComponent, computed, onMounted, watch } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import { accountStore } from '@/store/modules/account';
  import { modalStore } from '@/store/modules/modal';
  export default defineComponent({
    name: 'header',
    setup() {
      const { prefixCls } = useDesign('header');
      const loadingBar = computed(() => {
        return loadingBarStore.getLoadingState;
      });
      const account = computed(() => {
        const { connector, address } = accountStore.getAccountState;
        if (!connector || !connector.id || !address) {
          return '';
        }
        return address;
      });
      return { prefixCls, loadingBar, account, LoadingStatus };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls:~ '@{namespace}-header';
  .@{prefix-cls} {
    @apply pb-1;
    background-color: var(--color-body-bg);
    &__main {
      @apply flex flex-row justify-between items-center bg-black w-full;
      height: 58px;
    }
    &__content {
      &--left {
        @apply flex flex-row items-center;
        padding-left: 36px;
      }
    }
  }
</style>
