<template>
  <div v-if="open" class="mx-auto" :class="`${prefixCls}`">
    <div class="backdrop" @click="handleClose" />
    <div class="shell overflow-hidden anim-scale-in relative rounded" :class="{ mobile: isMobile }">
      <slot />
      <a @click="handleClose" class="block cursor-pointer absolute right-0 p-3">
        <svgIcon iconClass="close" class="close text-sm" />
      </a>
    </div>
  </div>
</template>

<script lang="ts">
  import { useDesign } from '@/hooks/useDesign';
  import { useIsMobile } from '@/hooks/useIsMobile';
  import { defineComponent } from 'vue';
  export default defineComponent({
    props: {
      open: Boolean,
    },
    emits: ['close'],
    setup(_, { emit }) {
      const { prefixCls } = useDesign('modal');
      const { isMobile } = useIsMobile();
      const handleClose = () => {
        emit('close');
      };
      return { prefixCls, isMobile, handleClose };
    },
  });
</script>

<style lang="less" scoped>
  @prefix-cls: ~'@{namespace}-modal';
  .@{prefix-cls} {
    position: fixed;
    display: flex;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    align-items: center;
    justify-content: center;
    z-index: 10;

    .backdrop {
      position: fixed;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 99;
      background: rgba(0, 0, 0, 0.4);
    }

    .shell {
      display: flex;
      flex-direction: column;
      z-index: 999;
      margin: 0 auto;
      width: 100%;
      max-height: 90%;
      background-color: #fff;
      // @media (max-width: 767px) {
      //   width: 100% !important;
      //   max-width: 100% !important;
