
  import { defineComponent } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import { loadingBarStore, LoadingStatus } from '@/store/modules/loadingBar';
  import { notifyStore, NotifyType } from '@/store/modules/notifications';
  export default defineComponent({
    name: 'connectors',
    props: {
      open: {
        type: Boolean,
      },
    },
    setup(_, { emit }) {
      const { prefixCls } = useDesign('connectors');
      function handleClose() {
        emit('close');
      }
      async function handleLogin() {
        handleClose();
        if (window['ethereum']) {
          // provider = window['ethereum'];
          try {
            const res = await window['ethereum'].enable();
            console.log(res);
          } catch (e) {
            notifyStore.commitNotify([e.message, NotifyType.ERROR]);
            return;
            // if (e.code === 4001) return;
          }
        } else if (window['web3']) {
          // provider = window['web3'].currentProvider;
        } else {
          notifyStore.commitNotify([
            '请先在谷歌浏览器上安装metamask插件并导入自己的以太坊钱包',
            NotifyType.ERROR,
          ]);
          return;
        }
        try {
          loadingBarStore.setLoading(LoadingStatus.CONNECTOR);
          const result = await accountStore.connect('injected');
        } catch (error) {
          console.error(error.msg);
        } finally {
          loadingBarStore.setLoading(LoadingStatus.NONE);
        }
      }
      return { prefixCls, handleClose, handleLogin };
    },
  });
