export enum ValidationError {
  NONE,
  EMPTY,
  NOT_A_NUMBER,
  NOT_POSITIVE,
}

export enum PositionValidation {
  NONE,
  EMPTY_INPUT,
  INVALID_INPUT,
  NO_ACCOUNT,
  WRONG_NETWORK,
  INSUFFICIENT_BALANCE,
  TOO_MIN,
  TOO_MAX,
}
export enum InputType {
  VOLUME,
  AMOUNT,
}
export enum Type {
  Connect,
  Unlock,
  Trade,
}
export enum positionType {
  BUY,
  SELL,
}
export function validateNumberInput(input: string): ValidationError {
  if (!input) {
    return ValidationError.EMPTY;
  }
  const number = parseFloat(input);
  if (!number) {
    return ValidationError.NOT_A_NUMBER;
  }
  if (number <= 0) {
    return ValidationError.NOT_POSITIVE;
  }
  return ValidationError.NONE;
}

export function formatError(error: ValidationError): string {
  if (error === ValidationError.EMPTY) return '请输入金额';
  if (error === ValidationError.NOT_A_NUMBER) return '必须输入数字';
  if (error === ValidationError.NOT_POSITIVE) return '输入的值应为正数';
  return '';
}
