import { Contract, Overrides } from '@ethersproject/contracts';
import { Web3Provider } from '@ethersproject/providers';
import clearingHouseAbi from '@/helper/abi/ClearingHouse.json';
import { BigNumber } from '@ethersproject/bignumber';
import config from '@/config';

export default class ClearingHouse {
  /**
   * @description: 开仓
   * @param {Web3Provider} provider
   * @param {string} address
   * @param {BigNumber} size
   * @param {BigNumber} amount
   * @param {BigNumber} leverage
   * @param {BigNumber} amountLimit
   */
  static async openPosition(
    provider: Web3Provider,
    address: string,
    size: BigNumber,
    amount: BigNumber,
    leverage: BigNumber,
    amountLimit: BigNumber
  ) {
    try {
      const contract: Contract = new Contract(
        config.address.clearingHouse,
        clearingHouseAbi,
        provider.getSigner()
      );
      return await contract.openPosition(
        address,
        size,
        { d: amount },
        { d: leverage },
        { d: amountLimit }
      );
    } catch (error) {
      return Promise.reject(error);
    }
  }
  /**
   * @description: 增加保证金
   * @param {Web3Provider} provider
   * @param {string} address
   * @param {BigNumber} amount
   * @return {*}
   */
  static async addMargin(provider: Web3Provider, address: string, amount: BigNumber) {
    try {
      const contract: Contract = new Contract(
        config.address.clearingHouse,
        clearingHouseAbi,
        provider.getSigner()
      );
      return await contract.addMargin(address, { d: amount });
    } catch (error) {
      return Promise.reject(error);
    }
  }
  /**
   * @description: 减少保证金
   * @param {Web3Provider} provider
   * @param {string} address
   * @param {BigNumber} amount
   * @return {*}
   */
  static async removeMargin(provider: Web3Provider, address: string, amount: BigNumber) {
    try {
      const contract: Contract = new Contract(
        config.address.clearingHouse,
        clearingHouseAbi,
        provider.getSigner()
      );
      return await contract.removeMargin(address, { d: amount });
    } catch (error) {
      return Promise.reject(error);
    }
  }
  /**
   * @description: 平仓
   * @param {Web3Provider} provider
   * @param {string} address
   * @param {BigNumber} amount
   * @return {*}
   */
  static async closePosition(
    provider: Web3Provider,
    address: string,
    amount: BigNumber,
    overrides?: Overrides
  ) {
    try {
      const contract: Contract = new Contract(
        config.address.clearingHouse,
        clearingHouseAbi,
        provider.getSigner()
      );
      return await contract.closePosition(address, { d: amount }, overrides);
    } catch (error) {
      return Promise.reject(error);
    }
  }
}
