
  import { computed, defineComponent, onMounted, ref } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { getMsgListApi, setMsgReadApi } from '@/api/trade/msssage';
  import { dataFormate } from '@/helper/utils';
  export default defineComponent({
    name: 'messageLists',
    setup() {
      const { prefixCls } = useDesign('message');
      const lists = ref([]);
      const hideRead = ref(false);
      const filterList = computed(() => {
        if (!hideRead.value) return lists.value;
        return lists.value.filter((item: any) => !item.status);
      });
      async function handleRead(msgId) {
        try {
          const result = await setMsgReadApi({ mid: msgId });
          lists.value.map((item: any) => (item.status = 1));
        } catch (error) {
          console.error(error);
        }
      }
      onMounted(async () => {
        const result = await getMsgListApi({ page: 1, limit: 5 });
        lists.value = result.list;
      });
      return { prefixCls, filterList, dataFormate, hideRead, handleRead };
    },
  });
