import { VuexModule, getModule, Module, Mutation } from 'vuex-module-decorators';
import store from '@/store';
const NAME = 'loadingBar';

export enum LoadingStatus {
  NONE,
  POSITION,
  CONNECTOR,
  ADJUST_MARGIN,
  CLOSE_POSITION,
}
// duplicate namespace loadingBar/ for the namespaced module loadingBar
@Module({ dynamic: true, namespaced: true, store, name: NAME })
class LoadingBar extends VuexModule {
  private loadingState: LoadingStatus = LoadingStatus.NONE;
  get getLoadingState(): LoadingStatus {
    return this.loadingState;
  }

  @Mutation
  setLoading(type: LoadingStatus): void {
    this.loadingState = type;
  }
}
export const loadingBarStore = getModule<LoadingBar>(LoadingBar);
