
  import { defineComponent, computed, onMounted, onBeforeUnmount } from 'vue';
  import { useDesign } from '@/hooks/useDesign';
  import { accountStore } from '@/store/modules/account';
  import { modalStore } from '@/store/modules/modal';
  import Storage from '@/utils/storage';
  export default defineComponent({
    name: 'accountStatus',
    setup() {
      const { prefixCls } = useDesign('wallet');
      let timer: any;
      const handleLogin = () => {
        accountStore.connect('injected');
      };
      const loading = computed(() => {
        // return accountStore.getAuthLoading;
        return false;
      });
      function handleConnectors() {
        modalStore.setConnectorModalState(true);
      }
      onMounted(() => {
        const flag = Storage.get('connectorModal');
        if (flag === 'pageReload') {
          timer = setTimeout(() => {
            modalStore.setConnectorModalState(true);
            Storage.remove('connectorModal');
          }, 2500);
        }
      });
      onBeforeUnmount(() => {
        clearTimeout(timer);
        Storage.remove('connectorModal');
      });
      return { prefixCls, handleConnectors, loading };
    },
  });
