<template>
  <div class="fixed right-0 bottom-0 top-auto text-center mr-2" style="z-index: 99;">
    <transition-group name="fade" tag="div" :class="prefixCls" class="mb-4">
      <!-- @todo todo -->
      <!-- @tag tag -->
      <!-- @note note -->
      <!-- @fix fix -->
      <!-- @done done -->
      <!-- @done add notification type -->
      <template v-for="(item, key) in items">
        <div
          :class="`${prefixCls}__item`"
          :key="item"
          v-if="now < item.timestamp + duration && !item.hide"
        >
          <div class="py-4 flex-1 flex flex-row">
            <div class="icon-wrap text-2xl" v-if="item.type !== NotifyType.NONE">
              <svgIcon
                class="success"
                v-if="item.type === NotifyType.SUCCESS"
                iconClass="notifySucc"
              />
              <svgIcon class="info" v-if="item.type === NotifyType.INFO" iconClass="notifyInfo" />
              <svgIcon
                class="warning"
                v-if="item.type === NotifyType.WARNING"
                iconClass="notifyWarn"
              />
              <svgIcon
                class="error"
                v-if="item.type === NotifyType.ERROR"
                iconClass="notifyError"
              />
            </div>
            <div class="flex-1">
              <div class="message">
                <div>提示</div>
                <svgIcon iconClass="close" class="close" @click="handleHide(key)" />
              </div>
              <div class="description"> {{ item.message }}</div>
            </div>
          </div>
        </div>
      </template>
    </transition-group>
  </div>
</template>

<script lang="ts">
  const DURATION = 4000;
  import { defineComponent, computed, onMounted, ref } from 'vue';
  import { notifyStore, NotifyType } from '@/store/modules/notifications';
  import { useDesign } from '@/hooks/useDesign';
  export default defineComponent({
    data() {
      return {
        duration: DURATION,
      };
    },
    setup() {
      const { prefixCls } = useDesign('notification');
      const now = ref(Date.now());
      let timer: any = null;
      const items = computed<any>(() => {
        return notifyStore.getNotifyState;
      });

      function handleHide(index: number): void {
        notifyStore.setNotifyState(index);
      }
      onMounted(() => {
        timer = setInterval(() => (now.value = Date.now()), 60);
      });
      return { prefixCls, items, now, handleHide, NotifyType };
    },
  });
</script>
<style lang="less" scoped>
  @prefix-cls: ~'@{namespace}-notification';
  .@{prefix-cls} {
    &__item {
      width: 384px;
      max-width: calc(100vw - 48px);
      margin-bottom: 16px;
      margin-left: auto;
      overflow: hidden;
      word-wrap: break-word;
      background: #fff;
      border-radius: 4px;
      padding: 0 24px;
      line-height: 1.5715;
      box-shadow: 0 3px 6px -4px rgb(0 0 0 / 12%), 0 6px 16px 0 rgb(0 0 0 / 8%),
        0 9px 28px 8px rgb(0 0 0 / 5%);
      .message {
        @apply flex flex-row justify-between items-center;
        margin-bottom: 8px;
        color: rgba(0, 0, 0, 0.85);
        font-size: 16px;
        line-height: 24px;
      }
      .icon-wrap {
        height: 24px;
        margin-right: 10px;
        @apply flex items-center;
        .success {
          fill: var(--success-color);
        }
        .info {
          fill: var(--info-color);
        }
        .warning {
          fill: var(--warning-color);
        }
        .error {
          fill: var(--error-color);
        }
      }
      .close {
        font-size: 14px;
        cursor: pointer;
        &:hover {
          fill: #9b9aaf;
        }
      }
      .description {
        @apply text-sm text-left leading-5;
      }
    }
  }
  .fade-enter-from {
    transform: translateX(100%);
  }
  .fade-move {
    transition: transform 0.12s;
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: all 0.2s cubic-bezier(0, 0, 0.58, 1);
  }
  .fade-leave-to {
    opacity: 0;
    transform: translateY(100%);
    margin: 0;
  }
</style>
